.post{
  #markdown-toc{
    border: 5px #c3c3c3;
    border-style: double;
    *{list-style:none;}
    
  }
  
  $font1:Lora,Palatino,Times,"Times New Roman",黑体,STHeiti,serif,微软雅黑,Arial, Helvetica, sans-serif,"SF Pro Display",Roboto,Noto,Arial,"PingFang SC","Hiragino Sans GB","Microsoft YaHei",sans-serif;
  $font2:Lora,Palatino,Times,"Times New Roman",serif,STSong,SimSun,NSimSun,STFangsong,FangSong,STZhongsong;
  $fontp:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  $fonts:STSong,SimSun,NSimSun,STFangsong,FangSong,STZhongsong;
	.author{}
	.affiliation{}
  .abstract{font-size:0.9rem;
    font-weight:100;
    margin: auto;
    width:80%;
    margin-bottom:2rem;
    line-height:1.4;
    margin-top:3rem;
    font-family:$font1;
    p{
      margin-bottom:0.5em;
    
  }
  }
	h1,h2,h3,h4{
    font-family: $font1;
    }
    h2,h3,h4{
      color:#000033;
    }
  h1{
    color:#000066;
    text-align: center;
    line-height: 1.4;
    margin-bottom: 16px;
    font-size:2.4rem;}
  h2{
    font-weight: 700;
		line-height: 1.4;
		border-bottom: 1px solid #d5d5d5;
		margin-bottom: 8px;
		font-size: 1.4rem;
	}
	h3{
		font-size: 1.2rem;
		font-weight: 700;
		margin-bottom: 8px;
    
    &:before{
      content: '';
      display: block;
      width: 30px;
      height: 4px;
      margin-top: 40px;
      margin-bottom: 16px;
      background: #222;
    }

  }
  h4{ font-size: 1rem;
    font-weight: 700;
    margin-bottom: 8px;}
	body{
		p{
			word-wrap: break-word;
			margin: 0 0 28px;
			padding: 0;
			display: block;
			margin-block-start: 1rem;
			margin-block-end: 1rem;
			margin-inline-start: 0px;
			margin-inline-end: 0px;
			font-family: $font2;
			font-size: 1.7rem;
		}
		
	}
  .ref-text{
    word-break:break-all;
    font-family: $font2;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    font-size: 1.1rem;
    flex: 1;
    font-style: normal;
    padding-left: 12px;
    margin-bottom: 8px;
  }
  .ref-count{
    font-family: $font2;
    font-size: 1.4rem;
    line-height: 1.4;
    text-align: right;
    
  }
  .ref-li{
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    border-bottom: 1px solid #d5d5d5;
    padding-bottom: 16px;
    margin-bottom: 16px;
  }
  .a-ref{
    
    padding: 0;
    *{list-style: none;
    text-indent: 0;}
  
    ol{
      margin-top: 0;
      margin-bottom: 28px;
    }
  }
  .ref-link-u{
    text-align:right;
    -webkit-flex-basis: 100%;
    -ms-flex-preferred-size: 100%;
    flex-basis:100%;
    a{font-family: "Source Sans Pro",helvetica,arial,sans-serif,黑体,STHeiti;
      font-size: 1rem;
      font-weight: 650;}
    
  }
	a:hover{text-decoration :underline;}
	a{color:#07679a;word-break:break-all;}
  
  .fig{    
    
    padding: 20px 10px;
    padding-top: 20px;
    padding-right: 10px;
    padding-bottom: 20px;
    padding-left: 10px;
    max-width: 100%;
    margin-bottom: 24px;
    clear: both;}
      img {
        
        
      }
      figca,.figca{
      margin-top: .66667rem;
      padding: 0 1rem;
      font-size: .9rem;
      line-height: 1.5;
      text-align: center;
      color: #555;
      display:block;
      font-family:$font1;
  }
  p{font-family:$fontp;
  text-indent: 2em;
  }
  sup {
    top: -.5em;
}
sub {
    bottom: -.25em;
}
sub, sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
vertical-align: baseline;}
  .footnotes{
    padding-top: 0.3rem;
    text-indent: 0;
    margin:50px auto 20px 30px;
    border-top: 0.1rem dashed #000000;
    font-size: 0.9rem;
    ol{margin: 0 10%;}
    p{font-family:$font2;
      word-break: break-all;
      line-height: 1.1;
      word-break: break-all;
      margin-bottom: 0.5rem;}
      *{text-indent:0;}
}
}